import "core-js/modules/es.object.to-string";
import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
var routes = [{
  path: '/views/Home',
  name: 'Home',
  component: function component() {
    return import('../views/Home.vue');
  },
  children: [{
    path: '/views/invoice',
    name: 'Invoice',
    component: function component() {
      return import('../views/invoice.vue');
    },
    meta: {
      title: ''
    }
  }]
}, {
  path: '/views_mobile/Home',
  name: 'mobileHome',
  component: function component() {
    return import('../views_mobile/Home.vue');
  },
  // },
  children: [{
    path: '/views_mobile/invoice',
    name: 'mobileInvoice',
    component: function component() {
      return import('../views_mobile/invoice.vue');
    },
    meta: {
      title: ''
    }
  }]
}];
var router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: routes
});
export default router;