      console.log("It's Mobile");
      this.$router.push("/views_mobile/Home");
    }

    if (windowSize.height <= windowSize.width * 1.2) {
      //pathArr[1] == "mobile" && 
      // this.$router.push("/")
      // eslint-disable-next-line 
      console.log("It's PC");
      this.$router.push("/views/Home");
    }
  }
